.App {
  text-align: center;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(255, 255, 255);
  height: 100vh;
}
.inputArea {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.cart {
  margin: auto;
  margin-top: 50px;
}
.uppload {
  margin-top: 30px;
  background-color: white;
  border: solid 2px rgba(0, 0, 0, 0.5);
  padding: 5px 30px;
  z-index: 99;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.1s linear;
}
.uppload:hover {
  background-color: rgb(177, 177, 177);
  color: white;
}
.immagineCover {
  margin-top: 20px;
}
.ant-card {
  margin-top: 50px !important;
  height: 90%;
}
.cardaArea {
  width: 50%;
  display: flex;
  justify-content: center;
}
.inputArea input {
  border: 1px solid rgba(0, 0, 0, 0.1);
  margin: 20px 0px;
  padding: 10px;
}

.inputArea button {
  border: none;
  background-color: rgb(67, 180, 184);
  color: white;
  padding: 5px 30px;
  border-radius: 5px;
  transition: 0.1s linear;
  cursor: pointer;
}
.inputArea button:hover {
  background-color: rgba(67, 180, 184, 0.603);
}

.prezzi {
  display: flex;
}
.prezzi input {
  margin: 20px 10px;
}

@media screen and (max-width: 800px) {
  .App {
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    margin-bottom: 100px;
    width: 100%;
  }

  .prezzi input {
    width: 40%;
    margin: auto;
    margin-bottom: 40px;
    margin-top: 50px;
  }

  .header {
    margin-bottom: 100px;
  }

  .emailSingola {
    margin-left: 20px !important;
    width: 80% !important;
  }
}

.emailSingola {
  width: 90%;
  max-width: 500px;
  margin-left: 200px;
  margin-top: 50px;
}

.header ul {
  display: flex;
  justify-content: space-around;
  font-size: 18px;
  margin: 0;
  padding: 0;
  margin-top: 10px;
}

.prodottiOnline {
  display: flex;
  justify-content: left;
  flex-wrap: wrap;
}
.cardOnline {
  margin: 0 50px;
}
.container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.nuovaFoto{
  border: 1px solid black;
}
